.main{
    padding-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #000;
    padding-bottom: 112px;
}

.title{
    font-weight: 700;
    font-size: 48px;
    line-height: 57px;
    text-align: center;
    margin: 0;
    color: #fff;
}

.title2{
    margin: 0;
    font-weight: 700;
    font-size: 48px;
    line-height: 57px;
    text-align: center;
    color: #FCB500;
    margin-bottom: 85px;
}

.price{
    display: flex;
    align-items: center;
    margin-bottom: 50px;
}

.oldPrice{
    margin-right: 32px;
    font-weight: 400;
    font-size: 36px;
    line-height: 43px;
    text-align: center;
    color: #ADADAD;
    text-decoration: line-through;
}

.newPrice{
    font-weight: 400;
    font-size: 48px;
    line-height: 57px;
    color: #FFFFFF;
}


.bottomList{
    display: flex;
    align-items: flex-start;
    margin: 0;
    padding: 0;
    list-style: none;
    margin-bottom: 64px;
    li p{
        text-align: center;
    }
}

.bottomList{
    color: #fff;
}

.smallItem{
    width: 192px;
}

.smallItem img{
    width: 100%;
    margin-bottom: 78px;
}

.bigItem img{
    width: 100%;
    margin-bottom: 45px;
}

.bigItem{
    width: 462px;
    margin-right: 112px;
    margin-left: 112px;
    .firstDescr{
        font-weight: 400;
        font-size: 36px;
        line-height: 43px;
        color: #fff;
        margin-bottom: 8px;
    }

    .secondDescr{
        font-weight: 400;
        font-size: 24px;
        line-height: 28px;
        color: #FCB500;
    }
}

.smallItem{
    .firstDescr{
        font-weight: 400;
        font-size: 18px;
        line-height: 21px;
    }

    .secondDescr{
        font-weight: 400;
        font-size: 18px;
        line-height: 21px;
        color: #FCB500;
    }
}

.addBtn{
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    padding: 16px 32px;
    background: #FCB500;
    border-radius: 7px; 
    cursor: pointer;
}


@media (max-width:1240px) {
    .bigItem{
        width: 462px;
        margin-right: 50px;
        margin-left: 50px;
    }
}


@media (max-width:992px) {
    .bottomList{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .bottomList li{
        margin-bottom: 60px;
    }

    .bottom{
        width: 100%;
    }
}

@media (max-width:768px) {
    .title{
        font-size: 36px;
        line-height: 46px;
    }

    .title2{
        font-size: 36px;
        line-height: 46px;
    }

    .main{
        padding-top: 50px;
        padding-right: 24px;
        padding-left: 24px;
    }

}

@media (max-width:568px) {
    .title{
        font-size: 20px;
        line-height: 24px;
        margin-bottom: 5px;
    }

    .title2{
        font-size: 20px;
        line-height: 24px;
    }

    .main{
        padding-right: 10px;
        padding-left: 10px;
    }

    .bigItem{
        width: 257px;
        margin-right: 0px;
        margin-left: 0px;
    }


}
